<div class="toast-container p-3"><div class="toast align-items-center border-0 show" v-for="(toast, index) in toasts" :class="'text-bg-' + toast.type" role="alert" aria-live="assertive" aria-atomic="true"><div class="d-flex"><div class="toast-body">{{ toast.message }}</div><button class="btn-close btn-close-white me-2 m-auto" @click="onRemoveToastClick(index)" type="button" aria-label="Close"></button></div></div></div>